.user-list__login {
  position: relative;

  display: block;
  padding: 17px 21px;
  padding-left: 49px;

  line-height: 18px;
  color: #ffffff;
  text-decoration: none;
}
.user-list__login-icon {
  position: absolute;
  top: 17px;
  left: 21px;

  width: 16px;
  height: 16px;

  opacity: 0.3;

  fill: #ffffff;
}
@media (min-width: $tablet-width) {
  .user-list {
  	width: 60px;
  }
}
@media (min-width: $desktop-width) {
  .user-list {
  	width: 160px;
  }
}